@import "https://fonts.googleapis.com/css2?family=Montserrat:wght@400;500;600;700;800;900&family=Mountains+of+Christmas:wght@700&display=swap";
*, :before, :after {
  box-sizing: border-box;
}

html {
  tab-size: 4;
  -webkit-text-size-adjust: 100%;
  line-height: 1.15;
}

body {
  margin: 0;
  font-family: system-ui, AppleSystem, BlinkMacSystemFont, Segoe UI, Roboto, Noto Sans, Ubuntu, Cantarell, Helvetica Neue, AppleSystem, BlinkMacSystemFont, Segoe UI, Roboto, Noto Sans, Ubuntu, Cantarell, Helvetica Neue, -apple-system, Segoe UI, Roboto, Helvetica, Arial, sans-serif, Apple Color Emoji, Segoe UI Emoji;
}

hr {
  height: 0;
  color: inherit;
}

abbr[title] {
  -webkit-text-decoration: underline dotted;
  text-decoration: underline dotted;
}

b, strong {
  font-weight: bolder;
}

code, kbd, samp, pre {
  font-family: ui-monospace, SFMono-Regular, Consolas, Liberation Mono, Menlo, monospace;
  font-size: 1em;
}

small {
  font-size: 80%;
}

sub, sup {
  vertical-align: baseline;
  font-size: 75%;
  line-height: 0;
  position: relative;
}

sub {
  bottom: -.25em;
}

sup {
  top: -.5em;
}

table {
  text-indent: 0;
  border-color: inherit;
}

button, input, optgroup, select, textarea {
  margin: 0;
  font-family: inherit;
  font-size: 100%;
  line-height: 1.15;
}

button, select {
  text-transform: none;
}

button, [type="button"], [type="reset"], [type="submit"] {
  -webkit-appearance: button;
}

::-moz-focus-inner {
  border-style: none;
  padding: 0;
}

:-moz-focusring {
  outline: 1px dotted ButtonText;
}

:-moz-ui-invalid {
  box-shadow: none;
}

legend {
  padding: 0;
}

progress {
  vertical-align: baseline;
}

::-webkit-inner-spin-button, ::-webkit-outer-spin-button {
  height: auto;
}

[type="search"] {
  -webkit-appearance: textfield;
  outline-offset: -2px;
}

::-webkit-search-decoration {
  -webkit-appearance: none;
}

::-webkit-file-upload-button {
  -webkit-appearance: button;
  font: inherit;
}

summary {
  display: list-item;
}

:root {
  --background-color: #84a178;
  --first-text-color: #fcfcfc;
  --second-text-color: #131313;
  --white: #fff;
  --yellow: #f8d24e;
  --green: #0a8817;
  --lite-yellow: #efd478;
  --lite-green: #aec6a5;
  --lite-orange: #d6936d;
}

h1, h2, h3, h4, h5, h6, p {
  margin: 0;
  padding: 0;
}

img {
  max-width: 100%;
  height: auto;
  display: block;
}

ul, ol {
  margin: 0;
  padding: 0;
  list-style: none;
}

a {
  text-decoration: none;
}

address {
  font-style: normal;
}

button {
  cursor: pointer;
  border: none;
  border-radius: 10px;
  display: block;
}

body {
  color: var(--first-text-color);
  letter-spacing: .04em;
  cursor: url("vector.fcc71345.png") 1 5, auto;
  background-color: #84a178;
  font-family: Montserrat, sans-serif;
}

.container {
  margin: 0 auto;
  padding: 0 16px;
}

@media screen and (min-width: 480px) {
  .container {
    width: 480px;
  }
}

@media screen and (min-width: 768px) and (max-width: 1199px) {
  .container {
    width: 768px;
    padding: 0 32px;
  }
}

@media screen and (min-width: 1200px) {
  .container {
    width: 1200px;
  }
}

.hidden {
  width: 1px;
  height: 1px;
  white-space: nowrap;
  -webkit-clip-path: inset(100%);
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  border: 0;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

.page-header__content {
  margin-left: auto;
  margin-right: auto;
  padding: 12px 20px 0;
}

@media screen and (min-width: 768px) {
  .page-header__content {
    padding: 20px 32px 0;
  }
}

@media screen and (min-width: 1200px) {
  .page-header__content {
    padding: 8px 16px 0;
    display: flex;
  }
}

.page-header__content__link {
  z-index: 999;
}

.page-header__logo {
  margin-right: 308px;
}

.page-header__button {
  z-index: 999;
  color: var(--second-text-color);
  background-color: var(--yellow);
  min-width: 152px;
  justify-content: center;
  align-items: center;
  padding: 12px 35px 12px 36px;
  font-family: inherit;
  font-size: 14px;
  font-weight: 600;
  line-height: 1.21;
  transition: color .25s cubic-bezier(.4, 0, .2, 1), background-color .25s cubic-bezier(.4, 0, .2, 1);
  display: flex;
  position: absolute;
  top: 24px;
  right: 32px;
}

@media screen and (max-width: 767px) {
  .page-header__button {
    display: none;
  }
}

@media screen and (min-width: 1200px) {
  .page-header__button {
    top: 28px;
    right: 16px;
  }
}

.page-header__button:hover {
  background-color: var(--green);
}

.page-header__button:focus {
  background-color: var(--green);
  color: var(--white);
}

.page-header__button-icon {
  fill: currentColor;
  margin-left: 4px;
}

.page-header__button-icon:focus {
  color: var(--white);
}

.desktop-nav {
  z-index: 999;
}

@media screen and (max-width: 1199px) {
  .desktop-nav {
    display: none;
  }
}

.desktop-nav__list {
  display: flex;
}

.desktop-nav__item:not(:last-child) {
  margin-right: 28px;
}

.desktop-nav__link {
  padding-top: 32px;
  padding-bottom: 13px;
  transition: color .25s cubic-bezier(.4, 0, .2, 1);
  display: block;
}

.desktop-nav__link:hover, .desktop-nav__link:focus {
  color: var(--yellow);
}

.desktop-nav__link {
  color: var(--first-text-color);
  font-family: inherit;
  font-size: 16px;
  font-style: normal;
  font-weight: 700;
  line-height: 1.25;
}

.menu-toggle {
  z-index: 999;
  color: var(--first-text-color);
  background-color: rgba(0, 0, 0, 0);
  border: none;
  outline: none;
  justify-content: center;
  align-items: center;
  margin: 0;
  padding: 0;
  transition: color .25s cubic-bezier(.4, 0, .2, 1);
  display: flex;
  position: absolute;
  top: 16px;
  right: 20px;
}

@media screen and (min-width: 768px) {
  .menu-toggle {
    top: 29px;
    right: 216px;
  }
}

@media screen and (min-width: 1200px) {
  .menu-toggle {
    display: none;
  }
}

.menu-toggle:hover, .menu-toggle:focus {
  color: var(--yellow);
}

.menu-toggle__icon-menu {
  fill: currentColor;
}

.menu-container {
  width: 100vw;
  height: 100vh;
  background-color: var(--background-color);
  z-index: 999;
  transition: transform .25s cubic-bezier(.4, 0, .2, 1);
  position: fixed;
  top: 0;
  left: 0;
  transform: translateX(100%);
}

.menu-container__link {
  position: absolute;
  top: 12px;
  left: 32px;
}

@media screen and (min-width: 768px) {
  .menu-container__link {
    top: 20px;
    right: 32px;
  }
}

.menu-container__button {
  color: var(--second-text-color);
  background-color: var(--yellow);
  min-width: 152px;
  justify-content: center;
  align-items: center;
  padding: 12px 35px 12px 36px;
  font-family: inherit;
  font-size: 14px;
  font-weight: 600;
  line-height: 1.21;
  transition: color .25s cubic-bezier(.4, 0, .2, 1), background-color .25s cubic-bezier(.4, 0, .2, 1);
  display: flex;
}

@media screen and (min-width: 768px) {
  .menu-container__button {
    display: none;
  }
}

.menu-container__button:hover {
  background-color: var(--green);
}

.menu-container__button:focus {
  background-color: var(--green);
  color: var(--white);
}

.menu-container__button-icon {
  fill: currentColor;
  margin-left: 4px;
}

.menu-container__button-icon:focus {
  color: var(--white);
}

.menu-container.is-open {
  transform: translateX(0);
}

.menu-container .menu-toggle {
  position: absolute;
  top: 16px;
  right: 20px;
}

@media screen and (min-width: 768px) {
  .menu-container .menu-toggle {
    top: 32px;
    right: 32px;
  }
}

